import React from 'react';
import './App.scss';
import {
    Avatar,
    Brand,
    Card,
    CardBody,
    Gallery,
    GalleryItem,
    Nav,
    NavList,
    Page,
    PageHeader,
    PageSection,
    PageSectionVariants,
    PageSidebar,
    TextContent,
    Text, Title
} from '@patternfly/react-core';
import {BrowserRouter as Router, Switch, Route, NavLink, Redirect, BrowserRouter} from "react-router-dom";
import Audit from "./Audit/Audit";
import AuditDetail from "./Audit/AuditDetail";
import ScrollToTop from "./Shared/ScrollToTop";
const imgBrand = "data:image/svg+xml;base64,";
const imgAvatar = "data:image/svg+xml;base64,";

type appProps = {};
type appState = {
    activeItem: number,
    isMobileView: boolean,
    isNavOpenDesktop: boolean,
    isNavOpenMobile: boolean
};
type navItem = {
    groupId: number | string,
    itemId: number | string,
    to: string,
    event: React.FormEvent<HTMLInputElement>
};
class App extends React.Component<appProps, appState> {
    constructor(props: appProps) {
        super(props);
        this.state = {
            activeItem: 0,
            isMobileView: false,
            isNavOpenDesktop: true,
            isNavOpenMobile: false
        };
    }

    onNavSelect = (result: navItem) => {
        this.setState({
            activeItem: (typeof result.itemId === "number") ? result.itemId : parseInt(result.itemId)
        });
    };

    onNavToggleDesktop = () => {
        this.setState({
            isNavOpenDesktop: !this.state.isNavOpenDesktop
        });
    };

    onNavToggleMobile = () => {
        this.setState({
            isNavOpenMobile: !this.state.isNavOpenMobile
        });
    };

    render() {
        const {
            isNavOpenDesktop,
            isNavOpenMobile,
            isMobileView
        } = this.state;

        const PageNav = (
            <Nav onSelect={this.onNavSelect} aria-label="Nav" theme="dark">
                <NavList>
                    <li className="pf-c-nav__item">
                        <NavLink className="pf-c-nav__link" activeClassName="pf-m-current" to="/dashboard">Domain Monitoring</NavLink>
                    </li>
                    <li className="pf-c-nav__item">
                        <NavLink className="pf-c-nav__link" activeClassName="pf-m-current" to="/op-dashboard">Operational Monitoring</NavLink>
                    </li>
                    <li className="pf-c-nav__item">
                        <NavLink className="pf-c-nav__link" activeClassName="pf-m-current" to="/audit">Audit Investigation</NavLink>
                    </li>
                </NavList>
            </Nav>
        );

        const Header = (
            <PageHeader
                logo={<Brand src={imgBrand} alt="Patternfly Logo" />}
                avatar={<Avatar src={imgAvatar} alt="Avatar image" />}
                showNavToggle
                onNavToggle={isMobileView ? this.onNavToggleMobile : this.onNavToggleDesktop}
                isNavOpen={isMobileView ? isNavOpenMobile : isNavOpenDesktop}
            />
        );
        const Sidebar = (
            <PageSidebar nav={PageNav} isNavOpen={isMobileView ? isNavOpenMobile : isNavOpenDesktop} theme="dark" />
        );
        const pageId = 'main-content-page-layout-manual-nav';

        return (
            <React.Fragment>
                <Router>
                    <BrowserRouter basename="/xai-poc" />
                    <ScrollToTop />
                    <Page
                        header={Header}
                        sidebar={Sidebar}
                        mainContainerId={pageId}
                    >
                        <Switch>
                            <Route exact path="/">
                                <Redirect to="/dashboard" />
                            </Route>
                            <Route path="/dashboard">
                                <PageSection variant={PageSectionVariants.light} isFilled={true}>
                                    <TextContent>
                                        <Title size="4xl" headingLevel="h1">Domain Monitoring Dashboard</Title>
                                        <Text component="p">
                                            Here will be the monitoring dashboard
                                        </Text>
                                    </TextContent>
                                </PageSection>
                                <PageSection style={{height: "50em"}} isFilled={true}>
                                    <Gallery gutter="md">
                                        {Array.apply(0, Array(20)).map((x, i) => (
                                            <GalleryItem key={i}>
                                                <Card>
                                                    <CardBody>This is a card</CardBody>
                                                </Card>
                                            </GalleryItem>
                                        ))}
                                    </Gallery>
                                </PageSection>
                            </Route>
                            <Route path="/op-dashboard">
                                <PageSection variant={PageSectionVariants.light}>
                                    <TextContent>
                                        <Title size="4xl" headingLevel="h1">Operational Monitoring Dashboard</Title>
                                        <Text component="p">
                                            Here will be the monitoring dashboard
                                        </Text>
                                    </TextContent>
                                </PageSection>
                                <PageSection style={{height: "50em"}} isFilled={true}>
                                    <Gallery gutter="md">
                                        {Array.apply(0, Array(20)).map((x, i) => (
                                            <GalleryItem key={i}>
                                                <Card>
                                                    <CardBody>This is a card</CardBody>
                                                </Card>
                                            </GalleryItem>
                                        ))}
                                    </Gallery>
                                </PageSection>
                            </Route>

                            <Route exact path="/audit">
                                <Audit/>
                            </Route>
                            <Route path="/audit/:id">
                                <AuditDetail />
                            </Route>
                        </Switch>
                    </Page>
                </Router>
            </React.Fragment>
        );
    }
}

export default App;
